.navbar {
  background: rgb(52, 16, 214);
  min-height:70px;
  position:fixed;
  width:100%;
  -webkit-box-shadow:0 14px 28px rgba(0,0,0,.25),0 10px 10px rgba(0,0,0,.22);
  box-shadow:0 14px 28px rgba(0,0,0,.25),0 10px 10px rgba(0,0,0,.22);
  color:#fff;font-size:calc(10px + 14 * (100vw - 320px)/ 800);
  z-index:99;
  white-space:nowrap
  }
  
.navbar ul {
  margin:0;
  padding:0
}

.navbar li {
  display:inline-block;
  padding:26px 0;
  width:33.3%;text-align:center
}

.navbar a {
  text-decoration:none;
  color:#fff;
  font-weight:900
}

@media (max-width:750px){
  .click-item{
    margin:10px
  }
}
  
@media (max-width:500px){
  .click-item{
    margin:10px
  }
}
  